  auth: Auth;
  proctoring: Proctoring;
  FC_GetProctoringRoom: FT_GetProctoringRoom;
  FC_SetActiveCandidates: FT_SetActiveCandidates;
  FC_SetOneActiveCandidate: FT_SetOneActiveCandidate;
