  FC_ContinueExam: FT_ContinueExam;
  FC_StopCandidate: FT_StopCandidate;
  FC_CandidateExamEvent: FT_CandidateExamEvent;
  FC_ClearExamToRoomProctoring: FT_ClearExamToRoomProctoring;
